	toRad = num => {
		return num * Math.PI/180;
	}

	calculateDistance = (lat1, lng1, lat2, lng2) => {
		var dLat = this.toRad(lat2 - lat1);
		var dLng = this.toRad(lng2 - lng1);

		var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);

		var distance = EARTH_RADIUS * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
		//console.log(distance); // в метрах
		return distance;
	}

	calculateAngle = (lat1, lng1, lat2, lng2) => {

		var dLng = (lng2 - lng1);

		var y = Math.sin(dLng) * Math.cos(lat2);
		var x = Math.cos(lat1) * Math.sin(lat2) - Math.sin(lat1) * Math.cos(lat2) * Math.cos(dLng);

		var brng = Math.atan2(y, x);

		var pi = Math.PI;
		brng = brng * (180/pi);
		brng = (brng + 360) % 360;
	    brng = 360 - brng; // count degrees counter-clockwise - remove to make clockwise

	    return brng;

	}

